/* GAMES & QUIZZES*/

.quizzesAnimation {
  animation: quizzesKeyFrame 1s ease-in-out 1;
}

@keyframes quizzesKeyFrame {
  from {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}

/* IMG ANIMATION  */

.avatarAnimation {
  animation: avatarKeyFrame 1s ease-in-out 1;
}

@keyframes avatarKeyFrame {
  0% {
    transform: translate(0rem, 0rem);
  }
  50% {
    transform: translate(-7.375rem, -0rem);
  }
  100% {
    transform: translate(-7.375rem, -2.188rem);
  }
}

/* AVATAR NAME */

.fadeIn-avatarName {
  animation: fadeInAvatarNameKeyFrame 1s ease-in-out 0.5s 1;
}

@keyframes fadeInAvatarNameKeyFrame {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* LOADER ANIMATION */

.typing .dot {
  animation: mercuryTypingAnimation 1.5s infinite ease-in-out;
  background-color: var(--typing-animation-dot-color1);
  border-radius: 50%;
  height: 0.438rem;
  margin-right: 0.25rem;
  vertical-align: middle;
  width: 0.438rem;
  display: inline-block;
}

.typing .dot:nth-child(1) {
  animation-delay: 200ms;
}

.typing .dot:nth-child(2) {
  animation-delay: 300ms;
}

.typing .dot:nth-child(3) {
  animation-delay: 400ms;
}

.typing .dot:last-child {
  margin-right: 0;
}

@keyframes mercuryTypingAnimation {
  0% {
    transform: translateY(0rem);
    background-color: var(--typing-animation-dot-color2);
  }
  28% {
    transform: translateY(-0.438rem);
    background-color: var(--typing-animation-dot-color3);
  }
  44% {
    transform: translateY(0rem);
    background-color: var(--typing-animation-dot-color4);
  }
}

/* ANIMATION FOR BUBBLE WOBBLE */

.jello-horizontal {
  animation: jello-horizontal 4s linear infinite 1s;
}

@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }
  7.5% {
    transform: scale3d(1.25, 0.75, 1);
  }
  10% {
    transform: scale3d(0.75, 1.25, 1);
  }
  12.5% {
    transform: scale3d(1.15, 0.85, 1);
  }
  16.25% {
    transform: scale3d(0.95, 1.05, 1);
  }
  18.75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  25% {
    transform: scale3d(1, 1, 1);
  }
  50% {
    transform: scale3d(1, 1, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

/* STYLES FOR MESSAGES */

.chatBubblesUser {
  margin: 0rem 0.938rem 0.625rem 3.125rem;
  padding: 0.938rem;
  float: right;
  background-color: var(--light-blue);
  border-radius: 0.938rem 0.938rem 0 0.938rem;
  clear: both;
  color: var(--dark-blue);
  max-width: 80%;
  word-wrap: break-word;
  white-space: pre-wrap;
}

.chatUserYou {
  margin: 0rem 1.5rem 0rem 0rem;
  padding: 0rem;
  float: right;
  clear: both;
  color: var(--dark-blue);
  font-size: 0.75rem;
}

.chatBubblesBot {
  margin: 0.313rem 3.125rem 0rem 0.938rem;
  padding: 0.938rem;
  float: left;
  background-color: var(--dark-blue);
  color: var(--white);
  border-radius: 0.938rem 0.938rem 0.938rem 0;
  clear: both;
  white-space: pre-wrap;
  max-width: 80%;
  word-wrap: break-word;
}

.chatBotName {
  margin: 0rem 0rem 0rem 1.5rem;
  padding: 0rem;
  float: left;
  clear: both;
  color: var(--dark-blue);
  font-size: 0.75rem;
}

/* LOADER STYLE */

#loader {
  display: none;
}

.loader-bubble {
  background-color: var(--light-blue);
  padding: 0.75rem 1.375rem;
  border-radius: 1.25rem 1.25rem 1.25rem 0.125rem;
  display: inline-block;
  margin-left: 0.938rem;
}

.typing {
  align-items: center;
  display: flex;
  height: 1.063rem;
}

/* SCROLL BARR */

::-webkit-scrollbar {
  width: 0.344rem;
  padding-top: 1.25rem;
}

::-webkit-scrollbar-track {
  display: none;
}

::-webkit-scrollbar-thumb {
  background-color: var(--dark-blue);
  border-radius: 1.25rem;
}

::-webkit-scrollbar-track-piece {
  margin: 1.875rem 0;
}

/* POPUP MODAL STYLES */

.modal {
  display: none;
  position: fixed;
  z-index: 10;
  padding-top: 5%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  backdrop-filter: blur(6px);
}

.modal-content {
  display: flex;
  flex-direction: column;
  background-color: var(--modal-bckg-white);
  border-radius: 15px;
  margin: auto;
  padding: 2%;
  border: 1px solid var(--dark-blue);
  width: 90%;
  height: 90%;
  overflow: hidden;
}

.close {
  color: var(--dark-blue);
  margin-left: 100%;
  font-size: 28px;
  font-weight: bold;
  margin-top: -2%;
}

.close:hover,
.close:focus {
  color: var(--modal-close-hover-pink);
  text-decoration: none;
  cursor: pointer;
}

.popUp {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

/* QUIZZES STYLES */

#quizzesMessage {
  width: 14rem;
  padding-top: 1rem;
  margin: 0 auto 2rem auto;
  color: var(--dark-blue);
  font-family: "DIN Pro Bold", sans-serif;
  font-size: 1.563rem;
  text-align: center;
}

.chat-window {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.somethingWentWrongMessage {
  width: 30rem;
  margin: auto;
  color: #9d1662;
  font-family: "DIN Pro Bold", sans-serif;
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
}

.quizQuestion {
  padding: 0.313rem;
  font-size: 20px;
}

.quiz-hr {
  background-color: var(--modal-quizz-hr-dark);
  margin: 0 auto 0.938rem auto;
  padding: 0.094rem;
  border: none;
}

#questionsForm {
  font-size: 16px;
  display: flex;
  height: 100%;
  justify-content: center;
  align-content: center;
  overflow-y: auto;
  overflow-x: hidden;
}

.form-check:hover {
  background-color: var(--modal-form-check-blue);
  border-radius: 5px;
}

#submitBtn {
  background-color: var(--dark-blue);
  color: var(--white);
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  margin: auto;
  display: flex;
}

#submitBtn:hover {
  cursor: pointer;
  transform: scale(1);
  background-color: var(--btn-card-purple);
}

#quizSpan {
  font-size: 1.563rem;
  font-weight: bold;
}

/* GAMES STYLES */

.iframeContainer {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
}

.responsiveIframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

#gamesMessage {
  width: 14rem;
  padding-top: 1rem;
  margin: 0 auto 2rem auto;
  color: var(--dark-blue);
  font-family: "DIN Pro Bold", sans-serif;
  font-size: 1.563rem;
  text-align: center;
}

/* CONTACT-FORM STYLES */

input[type="text"],
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid var(--modal-form-games-border-gray);
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  font-family: "DIN Pro Light", sans-serif;
}

input[type="submit"] {
  background-color: var(--dark-blue);
  color: var(--white);
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: auto;
}

input[type="email"] {
  width: 100%;
  padding: 12px;
  border: 1px solid var(--modal-form-games-border-gray);
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  font-family: "DIN Pro Light", sans-serif;
}

.container {
  border-radius: 5px;
  padding: 20px;
  height: 100%;
}

.formHeader {
  padding-bottom: 2%;
  font-size: 150%;
  text-align: center;
}

.submit {
  display: flex;
  justify-self: center;
  padding-bottom: 2%;
}
